let WebIM = require("../../utils/WebIM")["default"];
let disp = require("../../utils/broadcast");

export default {
  data() {
    return {
      username: "",
      switchStatus: ""
    };
  },

  components: {},
  props: {},
  onLoad: function () {
    let myUsername = wx.getStorageSync("myUsername");
    let me = this;
    this.setData({
      username: myUsername
    });
    this.setData({
      switchStatus: WebIM.config.isDebug
    });
  },

  onShow() {
    this.setData({
      switchStatus: WebIM.config.isDebug
    });
  },

  methods: {
    openDebug(event) {
      WebIM.isDebug({
        isDebug: event.detail.value
      });
    },

    setData: function (obj, callback) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
      callback && callback();
    }
  }
};
